* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #333;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  font-size: 14px;
  display: flex;
}

.app {
  border: 2px solid #000;
  border-radius: 5px;
  padding: 30px 15px;
}

.app__question, .app__answer {
  text-align: center;
  font-size: 16px;
}

.app__answer {
  color: #fff;
  background-color: #0000;
  border: 0;
  outline: none;
}
